.canvas_wrapper{width:700px;display:flex;flex-direction:column;justify-content:center}.canvas_wrapper h2{font-size:20px;padding:10px;color:#283747;border-radius:10px;text-transform:uppercase;text-align:center;border:1px solid black}.canvas_wrapper .canvas_item{border-radius:10px;border:1px solid black}

.items__wrapper{width:200px;display:flex;flex-direction:column;justify-content:center;margin-right:15px}.items__wrapper h2{font-size:20px;padding:10px;color:#283747;border-radius:10px;text-transform:uppercase;text-align:center;border:1px dashed #616A6B}.items__wrapper p{font-size:14px;padding:10px;margin:0px 0px 10px;color:#283747;border-radius:10px;text-transform:uppercase;text-align:center;border:1px dashed #616A6B}.items__wrapper .items__container{width:200px;width:100%;display:flex;flex-direction:column;align-items:center;border:1px dashed #616A6B;border-radius:10px}

.item__circle{width:70px;height:70px;background-color:#28B463;border-radius:50%;border:1px solid grey;margin:10px}.item__square{width:70px;height:70px;background-color:#2E86C1;border:1px solid grey;margin:10px}

